import React, { useState } from "react";
import TemplateImage from "./Resources/Images/template.png";
import html2canvas from "html2canvas";
import Draggable from "react-draggable";
import "./App.css";

function App() {
  const [file, setFile] = useState<string>("");
  const [outputImage, setOutputImage] = useState<string>("");
  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    console.log("loaded1");
    if (e.target.files != null) {
      let reader = new FileReader();
      reader.readAsDataURL(e.target.files[0]);
      reader.onload = () => {
        console.log("loaded2");
        if (reader.result != null) {
          console.log("loaded3");
          setFile(reader.result as string);
        }
      };
    }
  };

  const downloadImage = () => {
    html2canvas(document.getElementById("AppDiv") as HTMLElement).then(
      function (canvas) {
        // document.body.appendChild(canvas);
        setOutputImage(canvas.toDataURL());
      }
    );
  };
  const clearUrl = (url: string) => url.replace(/^data:image\/\w+;base64,/, "");

  const downloadUrl = (url: string) => {
    let link = document.createElement("a");
    link.download = "ItemImage.png";
    link.href =
      "data:application/octet-stream;base64," + encodeURIComponent(url);
    document.body.appendChild(link);
    link.click();
    link.remove();
  };

  const [marginTop, setMarginTop] = useState<string>("414px");

  return (
    <div className="App">
      <div id="AppDiv">
        <img src={TemplateImage} alt="Template"></img>
        <div className="text discriptionDiv" contentEditable={true}>
          아이템 설명 작성
        </div>
        <input className="text mainText" placeholder="아이템 이름 작성" />
        <Draggable>
          <img className="itemImage" src={file} alt="" />
        </Draggable>
      </div>
      <div>
        <span>이미지 넣기 ➡ </span>
        <input type="file" name="file" onChange={(e) => handleFileChange(e)} />
        <button
          onClick={() => {
            setMarginTop("400px");
            html2canvas(document.getElementById("AppDiv") as HTMLElement).then(
              function (canvas) {
                // setOutputImage(canvas.toDataURL());
                let url = canvas.toDataURL();
                downloadUrl(clearUrl(url));
              }
            );
            // window.open(outputImage, "_blank");
            setMarginTop("414px");
          }}
        >
          Download Image
        </button>
        <input style={{ width: "100px" }} value={outputImage} />
      </div>
    </div>
  );
}

export default App;
